import {useState, useRef} from 'react'
// import Moment from 'react-moment'

// Date.prototype.addHours = function(h) {
//     this.setTime(this.getTime() + (h*60*60*1000));
//     return this;
//   }
// console.log(this)

const AddItem = ({onAdd}) => {
    const [text,setText] = useState('')
    const [pillnumber,setPillnumber] = useState('')
    const [timenumber,setTimenumber] = useState('')
    const [dose,setDose] = useState('')
    const [instance,setInstance] = useState('')
    const [reminder,setReminder] = useState(false)
    const inputElement = useRef()

    const onSubmit = (e) => {e.preventDefault()
    if (!text){
        alert('Please add a pill')
        return
    }
    onAdd({text, pillnumber, timenumber, dose, instance, reminder})

    setText('')
    setTimenumber('')
    setPillnumber('')
    setDose('')
    setInstance(pillnumber/dose)
    setReminder(false)
    // console.log(setInstance)
}
    return (
        <form className='add-form' onSubmit={onSubmit}>
            <div className='form-control'>
                <label>Pill Name</label>
                <input type = 'text'
                placeholder= 'Add Pill'
                value={text}
                onChange={(e) => setText(e.target.value)}/>
            </div>
            <div className='form-control'>
                <label>Total amount of pills per prescription</label>
                <input type = 'number'
                placeholder= 'i.e.30 pills'
                value={pillnumber}
                onChange={(e) => setPillnumber(e.target.value)}/>
            </div>
            <div className='form-control'>
                <label>Time interval (in hrs)</label>
                <span>Every <input type = 'number'
                placeholder= 'i.e. 6'
                value={timenumber}
                onChange={(e) => setTimenumber(e.target.value)}/>hours</span>
            </div>
            <div className='form-control'>
                <label>Dose</label>
                <input type = 'number'
                placeholder= 'i.e. 2'
                ref = {inputElement}
                value={dose}
                onChange={(e) => setDose(e.target.value)}/>
            </div>
            <div className='form-control form-control-check'>
                <label>Set Reminder</label>
                <input type = 'checkbox'
                checked = {reminder}
                value={reminder}
                onChange={(e) => setReminder(e.currentTarget.checked)}/>
            </div>
            <input type='submit' value='Save Pill' className='btn btn-block'/>
        </form>
    )
}

export default AddItem
